.card-container{
  padding: 3rem 0;
}
.card{
  background-color: rgb(233, 233, 233);
  border-radius: .5rem;
  box-shadow: 2px 2px 5px rgba(94, 94, 94, 0.76);
  overflow: hidden;
  width: calc(50% - 1rem);
  margin-bottom: 3rem
}
.card h3{
  font-size: 1.4rem
}
.card-text{
  padding: 1rem;
}
.card-text h3{
  text-align: center;
}
.subtitulo{
  font-size: 4rem;
  margin: 4rem 0 0 0;
}
.platillos .card-container{
  justify-content: space-around;
}
@media only screen and (min-width: 64rem){
   .card{
      width: calc(33.3% - 6rem);
      margin-bottom: 6rem
    }
    .subtitulo{
      font-size: 6rem;
      margin: 6rem 0 3rem 0;
    }
    .card h3{
      font-size: 2rem
    }
}
